.card {
    background-color: #fffefec8;
    border: 1px solid #eee;
    border-radius: 5px;
    /* box-shadow: 0 3px 10px rgb(0, 0, 0, .2); */
    box-shadow: 0 7px 10px #BF9563;
    padding: 1rem;
}

.card .areas-atuacao-icone{
    margin: auto;
    text-align: center;
}
.card .card-icon {
    border-radius: 5px;
    height: 2.5rem;
    width: auto;
    padding: .1rem;
    margin-top: -.75rem;
    color: #000000;
    /* background-color:  #472E2F; */
    /* box-shadow: 0 5px 15px rgb(0, 0, 0, .5); */
}

.card p {
    color: #000000;
    font-size: 3rem;
    /* font-weight: 100; */
    margin: 10px;
    text-align: center;
}

.card span {
    color: #472E2F;
    font-size: 1rem;
    font-weight: 700;
}

/* ******** cartoes ******** */
.cartao-quem-somos {
    margin: 1.5rem;
    text-align: center;
  }
  .cartao-quem-somos p {
    color: #000000;
    font-size: 1.2rem;
    margin: 5px;
  }
  .cartao-quem-somos span {
    font-size: larger;
    font-weight: bolder;
  }
  .objetivos .entre-contato {
    text-align: center;
  }

@media screen and (max-width: 940px) {
     .card {
        width: 70vw;
        text-align: center;
     }
}