/* **************** CSS for Whatsapp button **********/
.entre-contato {
  text-align: center;
}

/* ************************************************** */
/* ***** CSS For Hero Secition of the Home page ***** */
/* ************************************************** */
.hero .entre-contato {
  text-align: left;
}

.hero {
  background-color: var(--overlay);
  color: black;
  height: 100vh;
  width: 100%;
  position: relative;
}

.hero:before {
  content: '';
  background: url('../../images/cidade-image-fundo.jpg') no-repeat center
    center/cover;
  background-position: 1px 0px;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.hero .content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  padding: 2rem;
}

.hero .content p:first-child {
  color: #152B40;
  font-weight: 700;
  font-size: 1.75rem;
  text-shadow: 0 3px 10px #BF9563;
}

.hero .content p:nth-child(2) 
{
  margin-top: 1rem;
  font-weight: 500;
  font-size: 1.25rem;
}
/* ************************************** */
@media screen and (max-width: 600px) {
  .hero:before {
    background-position: -175px 1px;
  }
  .hero .content img {
    margin-top: 0%;
  }
  .hero .content p:nth-child(3) {
    font-size: 1.5rem;
  }
  .hero .content p:nth-child(4) {
    font-size: 1rem;
  }
  .hero .button {
    margin: 1rem 0;
    padding: 0.5rem;
    width: 200px;
    font-size: 1rem;
  }
}
/* ************************************************* */
/* ***************** Quem Somos ******************** */
/* ************************************************* */
.quem-somos{
  max-width: 1240px;
  margin: auto;
}
.quem-somos .equipe-quem-somos {
  margin: auto;
  display: grid;
  grid-template-columns: 65% 1fr;
}

.quem-somos .col-2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
}

.quem-somos img {
  width: 25vw;
  max-width: 360px;
  margin: auto;
  padding: 1rem;
}

.quem-somos p {
  margin: 1rem 0;
  font-size: 1.25rem;
}

.quem-somos h2 {
  font-size: 2rem;
  text-align: center;
  margin-top: 2.5rem;
}
/* ************************************** */
@media screen and (max-width: 940px) {
  .quem-somos {
    width: 100vw;
    padding: 0;
    margin: auto;
  }
  .quem-somos .col-2 {
    padding: .75rem;
    text-align: justify;
  }
  .quem-somos .container {
    width: 95%;
    grid-template-columns: 1fr;
  }
  .quem-somos img{
    width: 90vw;
  }
  .quem-somos .equipe-quem-somos {
    grid-template-columns: 1fr;
  }
}
/* ****************************************** */
/* *************** objetivoS ***************** */
.objetivos {
  background-image: url('../../images/textura-branca.jpg');
  width: 100%;
  margin: auto;
  padding: 1rem;
}

.objetivos .conteudo {
  text-align: justify;
}

.objetivos .container-cartoes {
  width: 100%;
  max-width: 1080px;
  margin: 0 auto;
  display: grid;
  padding: 15px 0px;
  grid-template-columns: repeat(3, 1fr);
}
/* ************************************** */
 @media screen and (max-width: 940px) {
  .objetivos {
    padding: 0;
  }
  .objetivos .container-cartoes{
    grid-template-columns: 1fr;
  }
  .objetivos .conteudo{
    padding: 1rem;
  }
} 

/* ************************************************ */
/* **************** Css for Works ***************** */
/* ************************************************ */
.works {
  width: 100%;
}

.works .container {
  width: 95vw;
  max-width: 1240px;
  margin: 0 auto 0 auto;
  padding: 4rem 0rem;
}

.works h2 {
  font-size: 2rem;
  text-align: center;
}

.works .container .content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 25px;
  margin: 4rem 5rem;
}

.works .entre-contato p {
  font-size: xx-large;
}
/* ************************************** */
@media screen and (max-width: 940px) {
  .works .container {
    width: 100%;
  }
  .works .container .content {
    grid-template-columns: 1fr;
    margin: 1rem 0;
  }
  .works .card {
    max-width: 90vw;
    margin: auto;
  }
}
/* EOF */
