/* ************************************************** */
/* ***** CSS For Hero Secition of the Home page ***** */
/* ************************************************** */
.hero-suspensão-cnh {
  background-color: rgba(0, 0, 0, 0.7);
  color: rgb(192, 131, 8);
  height: 100vh;
  width: 100%;
  position: relative;
}

.hero-suspensão-cnh:before {
  content: '';
  background: url('../../images/cnh-suspensa.jpg') no-repeat center center/cover;
  background-position: 1px 0px;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.hero-suspensão-cnh .content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2rem;
}

/* .hero-suspensão-cnh .content img {
  height: auto;
  margin: -50px 0 30px 5px;
} */

.hero-suspensão-cnh .content p:first-child {
  font-weight: 800;
  font-size: 2.5rem;
}

.hero-suspensão-cnh .content p:nth-child(2) {
  color: #fafafa;
  margin-top: 2rem;
  font-style: italic;
  font-weight: 300;
  font-size: 2rem;
}
/* ***************** Hero 2 ********************** */
.recorrer-cnh {
  background-color: rgba(0, 0, 0, 0.87);
  color: rgb(192, 131, 8);
  height: 100vh;
  width: 100%;
  position: relative;
}

.recorrer-cnh:before {
  content: '';
  background: url('../../images/office.jpg') no-repeat center center/cover;
  background-position: 1px 0px;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.recorrer-cnh .content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2rem;
}

.recorrer-cnh .content p:first-child {
  font-weight: 800;
  font-size: 3.5rem;
}

.recorrer-cnh .content p:nth-child(2) {
  color: #fafafa;
  margin-top: 2rem;
  font-style: italic;
  font-weight: 300;
  font-size: 1.5rem;
}


/************************************************/
/* **************** Css for About ***************** */
/* ************************************************ */
.cnh-about {
  background: #ffffff;
  margin: 2rem auto;
  /* padding: 1rem; */
  width: 100%;
}

.cnh-about .container {
  width: 90vw;
  max-width: 1240px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.cnh-about .consulte-botao-sobre-cnh{
  text-align: center;
}

.cnh-about h2 {
  font-size: 1.5rem;
  /* font-weight: 500; */
  text-align: center;
}

.cnh-about .col-2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem;
}

.cnh-about img {
  /* width: 15vw; */
  /* max-width: 500px; */
  /* margin-bottom: 2.5rem; */
  margin: auto;
  /* -webkit-transform: scaleX(-1); */
  /* transform: scaleX(-1); */
}

.cnh-about .col-2 p {
  margin: 1.5rem 0;
}

.cnh-about .card-text {
  text-align: justify;
}

.about .button {
  margin-top: 1.5rem;
  margin-bottom: 1rem;
}

/* Breaking points for mobile */
@media screen and (max-width: 840px) {
  .cnh-about .container {
    grid-template-columns: 1fr;
  }
  .cnh-about .container {
    width: 90vw;
  }
  .cnh-about .container img {
    padding: 0rem;
    width: 50vw;
    /* margin: auto; */
  }
  .cnh-about .container .col-2 {
    padding: 2rem 0;
    order: 1;
  }
  /* .cnh-about .card-text {
    text-align: justify;
  } */
  .cnh-about .container .button {
    margin-bottom: 1rem;
  }
}
@media screen and (max-width: 600px) {
  .hero-suspensão-cnh:before {
    background-position: -475px 1px;
  }
  .hero-suspensão-cnh .content {
    margin: auto;
  }
  .hero-suspensão-cnh .content p:first-child {
    padding-top: 5vh;
    font-size: 2rem;
  }
  .hero-suspensão-cnh .content p:nth-child(2) {
    font-size: 1.25rem;
  }
  /* ********** hero 2 *********** */
  .recorrer-cnh .content{
    margin: auto;
  }
  .recorrer-cnh .content p:first-child {
    font-size: 2rem;
  }
  .recorrer-cnh .content p:nth-child(2) {
    font-size: 1.25rem;
  }
}