/* ************************************************** */
/* ***** CSS For Hero Secition of the Home page ***** */
/* ************************************************** */
.hero-pix {
    background-color: rgba(0, 0, 0, 0.8);
    color: rgb(255, 255, 255);
    height: 100vh;
    width: 100%;
    position: relative;
  }
  
  .hero-pix:before {
    content: '';
    background: url('../../images/golpe-do-pix.jpg') no-repeat center
      center/cover;
    background-position: 1px 0px;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }
  .hero-pix .content {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: auto;
    padding: 2rem;
  }
  
  .hero-pix .content img {
    width: 30vw;
    height: auto;
    margin: -50px 0 30px 5px;
  }
  
  .hero-pix .content p:first-child {
    font-weight: 800;
    font-size: 3.5rem;
  }
  
  .hero-pix .content p:nth-child(2) {
    font-style: italic;
    font-weight: 300;
    font-size: 2rem;
  }
  
  .hero-pix .button {
    margin: 1rem 0;
    width: 300px;
    background-color: var(--primary);
    border: 1px solid var(--primary-dark);
  }
  
  .hero-pix .button:hover {
    background-color: var(--primary-dark);
    border: 1px solid var(--overlay);
  }
  @media screen and (max-width: 600px) {
    .hero-pix:before {
      background-position: -475px 1px;
    }
    .hero-pix .content img {
      margin-top: 0%;
    }
    .hero-pix .content p:nth-child(3) {
      font-size: 1.5rem;
    }
    .hero-pix .content p:nth-child(4) {
      font-size: 1rem;
    }
    .hero-pix .button {
      margin: 1rem 0;
      padding: 0.5rem;
      width: 200px;
      font-size: 1rem;
    }
  }
  