/* **************** Css for About ***************** */
/* ************************************************ */
.about {
  background: #ffffff;
  margin: auto;
  padding: 5rem 1rem 5rem 1rem;
  width: 100%;
}

.about .container {
  width: 90vw;
  max-width: 1240px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.about h2 {
  font-size: 3rem;
  text-align: center;
}

