/* Breaking points for mobile */
@media screen and (max-width: 640px) {
  .about .container {
    grid-template-columns: 1fr;
  }
  .about .container {
    width: 90vw;
  }
  .about .container img {
    padding: 0rem;
    width: 90vw;
    /* margin: auto; */
  }
  .about .container .col-2 {
    padding: 2rem;
  }
  .about .card-text {
    text-align: center;
  }
  .about .container .button {
    margin-bottom: 1rem;
  }
}
