.icon-wp{
  display: grid;
  column-gap: 1px;
  grid-template-columns: 15% auto;
}
.icon-wp .btn-text {
  margin: auto;
}

.button-wp {
  margin: 2rem 0;
  padding: .5rem;
  color: #fafafa;
  font-weight: bolder;
  width: 320px;
  letter-spacing: .8px;
  background-color: #1EA130;
  border: 1px solid #FAFAFA;
}
.button-wp:hover {
  background-color: #25D366;
  border: 1px solid #FAFAFA;
}
